import * as React from "react";
import { Link } from "gatsby";
import { LinkComponent } from "../interfaces/interfaces";

interface A extends LinkComponent {
    newTab?: boolean,
}

export function A({ to, title, children, newTab = true, className, id }: A): JSX.Element {
    return (
        <a className={ `default-link ${ className }` } id={ id }
           title={ title }
           href={ to }
           target={ newTab ? "_blank" : undefined }
           rel={ newTab ? "noopener" : undefined }>
            { children }
        </a>
    );
}

export function MyLink({ to, title, children, className, id }: LinkComponent): JSX.Element {
    return (
        <Link to={ to ?? "/" } id={ id }
              title={ title }
              className={ `default-link ${ className }` }>
            { children }
        </Link>
    );
}
